import { Button, Space, Table } from 'antd'
import React from 'react'

const TablePage = (props) => {

    const [filteredInfo, setFilteredInfo] = React.useState({})
    // const [data, setData] = React.useState([])
    // const [columns, setColumns] = React.useState([])
    const [sortedInfo, setSortedInfo] = React.useState({})

    const handleChange = (pagination, filters, sorter) => {
        console.log('Various parameters', pagination, filters, sorter);
        setFilteredInfo(filters)
        setSortedInfo(sorter)
    }

    const clearFilters = () => {
        setFilteredInfo(null)
    }

    let data = []
    let columns = []

    if (props.tableName === 'transport_general_information') {
        const TransportGeneralInformations = require('./StructureTables/TransportGeneralInformations')
        data = TransportGeneralInformations.data(props.transportGeneralInformationsData)
        columns = TransportGeneralInformations.columns()
    }

    if (props.tableName === 'transport_commissioning') {
        const transportCommissioning = require('./StructureTables/TransportCommissioning')
        data = transportCommissioning.data(props.transportCommissioningsData)
        columns = transportCommissioning.columns()
    }

    if (props.tableName === 'driver') {
        const drivers = require('./StructureTables/Drivers')
        data = drivers.data(props.drivers)
        columns = drivers.columns()
    }

    if (props.tableName === 'completeness') {
        const completeness = require('./StructureTables/Completeness')
        data = completeness.data(props.completeness)
        columns = completeness.columns()
    }

    if (props.tableName === 'work_accounting') {
        const workAccounting = require('./StructureTables/WorkAccounting')
        data = workAccounting.data(props.workAccounting)
        columns = workAccounting.columns()
    }
    console.log(props.tableName)

    if (props.tableName === 'tires') {
        const tires = require('./StructureTables/Tires')
        data = tires.data(props.tires)
        columns = tires.columns()
    }

    if (props.tableName === 'battery') {
        const battery = require('./StructureTables/Battery')
        data = battery.data(props.battery)
        columns = battery.columns()
    }

    if (props.tableName === 'maintenance') {
        const maintenance = require('./StructureTables/Maintenance')
        data = maintenance.data(props.maintenance)
        columns = maintenance.columns()
    }

    if (props.tableName === 'repairs') {
        const repairs = require('./StructureTables/Repairs')
        data = repairs.data(props.repairs)
        columns = repairs.columns()
    }

    return (
        <>
        {/* <Space style={{ marginBottom: 16 }}>
          <Button onClick={setAgeSort}>Sort age</Button>
      <Button onClick={clearFilters}>Clear filters</Button>
      <Button onClick={clearAll}>Clear filters and sorters</Button>
        ace> */}
            <Table columns={columns} dataSource={data} onChange={handleChange} />
        </>
    )
}
        
export default TablePage
