import React from 'react'

import { Tree } from 'antd';
import { Link, NavLink, Redirect } from 'react-router-dom';

const { DirectoryTree } = Tree;

const Navbar = (props) => {
  const treeSections = (magazineId) => props.magazines.sectionsData.filter(sectionData => sectionData.magazineId === magazineId).map(sectionData => {
    return {
      title: <NavLink exact to={`${sectionData.tableName}`}>{sectionData.name}</NavLink>,
      key: sectionData.magazineId + '-' + sectionData.id,
      isLeaf: true
    }
  })

  const treeMagazines = props.magazines.magazinesData.map(magazineData => {
    return {
      title: <NavLink exact to={`${magazineData.tableName}`}>{magazineData.name}</NavLink>,
      key: magazineData.id,
      children: treeSections(magazineData.id)
    }
  })

  const onSelect = (keys, info) => {
    console.log('Trigger Select', keys, info);
  };

  const onExpand = () => {
    console.log('Раскрыть папку');
  };

  return (
    <nav>
      <DirectoryTree
        multiple
        defaultExpandAll
        onSelect={onSelect}
        onExpand={onExpand}
        treeData={treeMagazines}
      />
    </nav>
  )
}

export default Navbar
