const magazinesReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_MAGAZINES_DATA:
            return {
                ...state,
                newsData: action.newsData
            }
        default:
            return state
    }
}

const setMagazines = (newsData) => {
    return { type: SET_MAGAZINES_DATA, newsData: newsData }
}

export const getMagazines = () => {
    return (setMagazines(initialState))
}

export default magazinesReducer