class Battery {
    data() {
        return [{
            key: '1',
            type: 'тип',
            model: 'модель',
            dateOfManufacture: 'дата изготовления',
            dateBegin: 'Дата установки, показания спидометра',
            dateEnd: 'Дата снятия, показания спидометра',
            actualServiceLife: 'фактический срок службы',
            signature: 'подпись',
            userId: 1,
            departamentId: 1
        }]
    }

    columns() {
        return ([
            {
                title: 'тип',
                dataIndex: 'type',
                key: 'type',
            },
            {
                title: 'model',
                dataIndex: 'model',
                key: 'model',
            },
            {
                title: 'dateOfManufacture',
                dataIndex: 'dateOfManufacture',
                key: 'dateOfManufacture',
            },
            {
                title: 'Дата установки, показания спидометра',
                dataIndex: 'dateBegin',
                key: 'dateBegin',
            },
            {
                title: 'Дата снятия, показания спидометра',
                dataIndex: 'dateEnd',
                key: 'dateEnd',
            },
            {
                title: 'фактический срок службы',
                dataIndex: 'actualServiceLife',
                key: 'actualServiceLife',
            },
            {
                title: 'Подпись',
                dataIndex: 'signature',
                key: 'signature',
            },
            {
                title: 'userId',
                dataIndex: 'userId',
                key: 'userId',
            },
            {
                title: 'departamentId',
                dataIndex: 'departamentId',
                key: 'departamentId',
            },
        ])
    }

}

module.exports = new Battery