import { connect } from 'react-redux';
import { Route, Switch } from 'react-router-dom';
import './App.css';
import Departaments from './components/Departament/Departaments';
import Header from './components/Header/Header';
import Navbar from './components/Navbar/Navbar';

import "antd/dist/antd.css";
import TablePage from './components/TablePage/TablePage';

function App(props) {
      
       return (
              <div className='app-wrapper'>
                     <Header />
                     <Navbar {...props} />
                     <div className='app-wrapper-content'>
                            <Switch>

                                   {props.magazines.magazinesData.map(magazineData =>
                                          <Route key={magazineData.tableName} path={'/' + magazineData.tableName}
                                                 render={() => <TablePage {...props.magazines} tableName={magazineData.tableName} />} />
                                   )}

                                   {props.magazines.sectionsData.map(sectionData =>
                                          <Route key={sectionData.tableName} path={'/' + sectionData.tableName}
                                                 render={() => <TablePage {...props.magazines} tableName={sectionData.tableName} />} />
                                   )}
                                   {/* <Route path='/magazine:id'
                                          render={() => <Departaments path='/arvps' nameOrg='Ангарский район водных путей и судоходства (АРВПиС)' />} />
                                   <Route path='/section:id'
                                          render={() => <Departaments path='/bsrvps' nameOrg='Байкало-Селенгинский район водных путей и судоходства (БСРВПиС)' />} /> */}
                            </Switch>
                     </div>
              </div>
       );
}

const mapStateToProps = (state) => ({
       departaments: state.departaments,
       magazines: state.magazines
})

export default connect(mapStateToProps, {

})(App)
