class Tires {
    data() {
        return [{
            key: '1',
            size: 'размер',
            serial: 'серийный номер',
            dateBegin: 'Дата установки, показания спидометра',
            dateEnd: 'Дата снятия, показания спидометра',
            mileage: 'пробег',
            causes: 'причины снятия',
            signature: 'подпись',
            userId: 1,
            departamentId: 1
        }]
    }

    columns() {
        return ([
            {
                title: 'размер',
                dataIndex: 'size',
                key: 'size',
            },
            {
                title: 'серийный номер',
                dataIndex: 'serial',
                key: 'serial',
            },
            {
                title: 'Дата установки, показания спидометра',
                dataIndex: 'dateBegin',
                key: 'dateBegin',
            },
            {
                title: 'Дата снятия, показания спидометра',
                dataIndex: 'dateEnd',
                key: 'dateEnd',
            },
            {
                title: 'пробег',
                dataIndex: 'mileage',
                key: 'mileage',
            },
            {
                title: 'причины снятия',
                dataIndex: 'causes',
                key: 'causes',
            },
            {
                title: 'Подпись',
                dataIndex: 'signature',
                key: 'signature',
            },
            {
                title: 'userId',
                dataIndex: 'userId',
                key: 'userId',
            },
            {
                title: 'departamentId',
                dataIndex: 'departamentId',
                key: 'departamentId',
            },
        ])
    }

}

module.exports = new Tires