class Repairs {
    data() {
        return [{
            key: '1',
            view: 'Вид',
           dateBegin: 'Дата поступления',
           dateEnd: 'Дата выхода',
           description: 'Описание',
           numberDoc: 'Номер документа оплаты',
           signature: 'подпись',  
           userId: 1,
            departamentId: 1  
        }]
    }

    columns() {
        return ([
            {
                title: 'Вид',
                dataIndex: 'view',
                key: 'view',
            },
            {
                title: 'Дата установки, показания спидометра',
                dataIndex: 'dateBegin',
                key: 'dateBegin',
            },
            {
                title: 'Дата снятия, показания спидометра',
                dataIndex: 'dateEnd',
                key: 'dateEnd',
            },
            {
                title: 'description',
                dataIndex: 'description',
                key: 'description',
            },
            {
                title: 'Номер документа оплаты',
                dataIndex: 'numberDoc',
                key: 'numberDoc',
            },
            {
                title: 'Подпись',
                dataIndex: 'signature',
                key: 'signature',
            },
            {
                title: 'userId',
                dataIndex: 'userId',
                key: 'userId',
            },
            {
                title: 'departamentId',
                dataIndex: 'departamentId',
                key: 'departamentId',
            },
        ])
    }

}

module.exports = new Repairs